import React from "react";
//import { moviesData } from "../moviesData";
import MovieItem from "./MovieItem";
import MovieTabs from "./MovieTabs";
import Pagination from "./Pagination";


import {API_URL, API_KEY_3} from "../utils/api";

// UI = fn(state, props)

// App = new React.Component()

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      movies: [],
      moviesWillWatch: [],
      sort_by: "popularity.desc",
      totalResults: 0,
      currentPage: 1, 
      limitPage: 10,
      pageSize: 20
    };
    
  }

  componentDidMount(){
  //  console.log("didMount");
    this.getMovies();
    
  }

  deleteMovie = movie => {
  //  console.log(movie.id);
    const updateMovies = this.state.movies.filter(item => item.id !== movie.id);
    console.log(updateMovies);

    
    this.setState({
      movies: updateMovies
    });
  };

  addMovieToWillWatch = movie => {
    const updateMoviesWillWatch = [...this.state.moviesWillWatch];
    updateMoviesWillWatch.push(movie);

    this.setState({
      moviesWillWatch: updateMoviesWillWatch
    });
  };

  deleteMovieFromWillWatch = movie => {
    const updateMoviesWillWatch = this.state.moviesWillWatch.filter(
      item => item.id !== movie.id
    );

    this.setState({
      moviesWillWatch: updateMoviesWillWatch
    });
  };

  updateSortBy = value => {
    this.setState({
      sort_by:  value
    }) 
    
  };
 componentDidUpdate(prevProps, prevState){
  //  console.log("didUpdate");
  //  console.log("prev:", prevProps, prevState);
  //  console.log("this:", this.props, this.state);
    if (prevState.sort_by !== this.state.sort_by) {
  //    console.log("call api");
      this.getMovies();
    }


 }
getMovies = () => {
fetch(`${API_URL}/discover/movie?api_key=${API_KEY_3}&sort_by=${this.state.sort_by}`).then((response)=> {
      console.log("then")
      return response.json()
    }).then((data) => {
    //  console.log("data", data);
      this.setState({
        movies: data.results, 
        totalResults: data.total_results
      });
    });
  
}
nextPage = (pageNumber) => {
  fetch(`${API_URL}/discover/movie?api_key=${API_KEY_3}&sort_by=${this.state.sort_by}&page=${pageNumber}`).then((response)=> {
      return response.json()
    }).then((data) => {
        this.setState({
        movies: data.results,
        currentPage: pageNumber,
        totalResults: data.total_results
      });
     
    });

  
}
  render() {
    //Количество страниц
    const totalPages = Math.floor(this.state.totalResults / this.state.pageSize);
    
  
    return (
      <div className="container">
        <div className="row mt-4">
          <div className="col-9">
            <div className="row mb-4">
              <div className ="col-12">   
                        <MovieTabs
                         sort_by={this.state.sort_by}
                         updateSortBy = {this.updateSortBy}
                         />
              </div>          
            </div>
            <div className="row">

              {this.state.movies.map(movie => {
                return (
                  <div className="col-6 mb-4" key={movie.id}>
                    <MovieItem
                      data={movie}
                      deleteMovie={this.deleteMovie}
                      addMovieToWillWatch={this.addMovieToWillWatch}
                      deleteMovieFromWillWatch={this.deleteMovieFromWillWatch}
                    />
                    
                  </div>
                );
              })}
              {this.state.totalResults > 20 ? <Pagination totalPages={totalPages}
                         nextPage={this.nextPage}
                         currentPage={this.state.currentPage}
                         limitPage={this.state.limitPage}
                         pageSize= {this.state.pageSize}
                         totalPages= {totalPages}
                          /> : "" }
            </div>
          </div>
          <div className="col-3">
            <h4>Will Watch: {this.state.moviesWillWatch.length} movies</h4>
            <ul className="list-group">
              {this.state.moviesWillWatch.map(movie => (
                <li key={movie.id} className="list-group-item">
                  <div className="d-flex justify-content-between">
                    <p>{movie.title}</p>
                    <p>{movie.vote_average}</p>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
