import React from "react";

const Pagination = (props) => {
let pager= {};	
let pageLinks = [];    
    

    const getPager= () => {
        
        let startPage =1,
            endPage = props.limitPage;
        
        if (props.totalPage <= props.limitPage) {
                // less than 10 total pages so show all
                startPage = 1;
                endPage = props.limitPage;
        } else {
                // more than 10 total pages so calculate start and end pages
                if (props.currentPage <= 6) {
                    startPage = 1;
                    endPage = props.limitPage;
                } else if (props.currentPage + 4 >= props.limitPage) {
                    startPage = props.currentPage -1;
                    endPage = props.currentPage - 1 + props.limitPage -1 ;
                } else {
                    startPage = props.currentPage - 5;
                    endPage = props.currentPage + 4;
                }
        }
       console.log(props.currentPage);
        console.log('startPage:', startPage);
        console.log('endPage:', endPage);
    
        // calculate start and end item indexes
        let startIndex = (props.currentPage - 1) * props.pageSize;
        let endIndex = Math.min(startIndex + props.pageSize - 1, props.pages - 1);

        // create an array of pages to ng-repeat in the pager control
    
        let pages = [...Array((endPage + 1) - startPage).keys()].map(i => startPage + i);
        pager = { 
            totalItems: props.totalItems,
            currentPage: props.nextPage,
            pageSize: props.pageSize,
            totalPages: props.totalPages,
            startPage: startPage,
            endPage: endPage,
            startIndex: startIndex,
            endIndex: endIndex,
            pages: pages
            
        };
        console.log('pages:', pager.pages);
        return pager;

}

const setPage = (page) => {
        pageLinks = [];
        if (page < 1 || page > pager.totalPages) {
            return;
        }
       
        pager = getPager();
        let start = pager.startPage;
        let end = pager.endPage;
        let next = page + 1;
        let previsios = page - 1;
        if (page === 1) {
            pageLinks.push(<li className={`page-item active`} key={1} onClick={() => props.nextPage(1)}>
            <a href="#" className="page-link">First</a></li>)
        }
        else if (page === pager.totalPages) {
             pageLinks.push(<li className={`page-item active`} key={1} onClick={() => props.nextPage(pager.totalPages)}>
            <a href="#" className="page-link">Last</a></li>)
             }
        
        
        
                     
        else  {
            
        /***    pageLinks.push(<li className={`page-item active`} key={1} onClick={() => props.nextPage(previsios)}>
            <a href="#" className="page-link">Previsios</a></li>);

        ***/            for(let i=start; i <= end; i++) {
                        let active = props.currentPage === i ? 'active' : '';

                        pageLinks.push(<li className={`page-item ${active}`} key={i} onClick={() => props.nextPage(i)}>
                        <a href="#" className="page-link">{i}</a></li>)
                      
                    }
         /***   pageLinks.push(<li className={`page-item active`} key={end+1} onClick={() => props.nextPage(next)}>
            <a href="#" className="page-link">Next</a></li>) 

        ***/           
            }
         
     console.log('pageLinks', pageLinks);
     return(pageLinks);
        

};
	return (
	
    <div className="container">
            <div className="row"> 
                <ul className="pagination">
                   {setPage(1)}
                   {setPage(pager.currentPage)}
                   {setPage(pager.totalPages)}
                </ul>
            </div>
    </div>     
	)
}


export default Pagination;